import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import StepIcon from "@mui/material/StepIcon";

const dispatchedEvents = [];

const SrsMuiStepIcon = function (props, ref) {
  return (
    <StepIcon
      active={props.active}
      completed={props.completed}
      error={props.error}
      icon={props.icon}
      alternativeLabel={props.alternativeLabel}
      theme={props.muiTheme}
    />
  );
};

const styles = `
  /* Custom styles for the MuiStepIcon component */
`;

const props = [
  {
    name: "active",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "completed",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "error",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "icon",
    defaultValue: null,
    type: "node",
  },
  {
    name: "alternativeLabel",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to StepIcon if needed
];

const slots = [];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiStepIcon),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
